.expenses-filter {
  color: white;
  padding: 0 1rem;
}

.expenses-filter-control {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  margin: 1rem 0;
}

.expenses-filter label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.expenses-filter select {
  font: inherit;
  padding: 0.5rem 3rem;
  font-weight: bold;
  border-radius: 6px;
}

@media (max-width: 581px) {
  .expenses-filter select {
    padding: 0 0.6rem;
    width: 5rem;
    height: 2rem;
  }

  .expenses-filter option {
    width: 0.1rem;
    height: 0.1rem;
    font-size: small;
  }
}
